import React, { Component } from "react";
import axios from 'axios';

import { Form, Button } from "react-bootstrap";

export default class MyRegisLoginForm extends Component  {

    constructor(props){
        
        super(props)

        this.onChangeUsername = this.onChangeUsername.bind(this);
        this.onChangeSecretcode = this.onChangeSecretcode.bind(this);
        this.onSubmit = this.onSubmit.bind(this);

        this.state = {
            username : '',
            secretcode: '',
            checklist: []
        }

    }

    onChangeUsername(e){
        this.setState({
            username: e.target.value
        })
    }

    onChangeSecretcode(e){
        this.setState({
            secretcode: e.target.value
        })
    }

    onSubmit(e){
        e.preventDefault();

        const user = {
            username: this.state.username,
            secretcode: this.state.secretcode,
            checklist: []
        }

        axios.post(process.env.REACT_APP_API_URL+'/api/users/enter', user)
            .then(res => {
                window.location = '/userid/'+res.data._id;
            });
    }

    render () {
        return (
            <Form onSubmit={this.onSubmit}>
                <Form.Group controlId="formBasicEmail">
                    <Form.Label>Alamat email</Form.Label>
                    <Form.Control type="email" placeholder="Masukkan alamat email Gmail" name="username" value={this.state.username} onChange={this.onChangeUsername}/>
                </Form.Group>

                <Form.Group controlId="formBasicPassword">
                    <Form.Label>Kode Rahasia</Form.Label>
                    <Form.Control type="password" placeholder="Masukan Kode Rahasia -- bukan password" name="secretcode" value={this.state.secretcode} onChange={this.onChangeSecretcode} />
                    <Form.Text className="text-muted">
                        Jangan masukkan password biasa kamu. Masukkan kode rahasia baru yang mudah kamu ingat. 
                    </Form.Text>
                </Form.Group>

                <Button variant="primary" type="submit">
                    Register / Login
                </Button>
            </Form>
        )
    }
}